                                            соискателя гранта (в случае если соискатель гранта является иностранным
                                            гражданином);
                                        </li>
                                        <li>ходатайства ученого совета научной или образовательной организации высшего
                                            образования, руководителя организации, осуществляющей научные исследования
                                            или разработки, в которой учится или работает соискатель гранта, о
                                            предоставлении гранта соискателю гранта, включающего в себя описание
                                            значимости результатов, полученных в рамках научного проекта, личного вклада
                                            соискателя гранта в достижение полученных результатов и согласие на
                                            предоставление соискателю гранта условий для выполнения научного проекта по
                                            теме гранта;
                                        </li>
