                            <b-tab title="Порядок рассмотрения заявок участников">
                                <b-card-text>
                                    <h3>Порядок рассмотрения заявок участников</h3>
                                    <p>Департамент в течение срока приема заявок осуществляет предварительный анализ
                                        заявок соискателей и документов на предмет правильности и полноты их
                                        представления и, в случае необходимости уточнения информации, осуществляет
                                        подготовку письменного уведомления, направляет его соискателю гранта посредством
                                        Системы на адрес электронной почты, указанный в заявке.</p>
                                    <p>Предварительный анализ осуществляется только в случае подачи соискателем гранта
                                        заявки не позднее чем за 10 дней до даты окончания срока приема заявок
                                        (включительно).</p>
                                    <p>Соискатель гранта вправе представить скорректированную (уточненную) заявку до
                                        даты окончания срока приема заявок (включительно). Непредставление соискателем
                                        гранта скорректированной (уточненной) заявки в указанный срок расценивается как
                                        отказ от участия в конкурсе.</p>
                                    <p>Департамент в течение 15 дней со дня окончания срока приема заявок принимает
                                        решение о признании соискателя гранта участником конкурса при отсутствии
                                        оснований, предусмотренных пунктом 2.9 Порядка, направляет письма о назначении
                                        экспертов.</p>
                                    <p>Экспертиза поданных на участие в конкурсе заявок и подготовка экспертного
                                        заключения осуществляется экспертами в Системе в течение 14 дней со дня
                                        назначения им заявок.</p>
                                    <p>Оценка заявок участников конкурса экспертами осуществляется по шкале от 0 до 10
                                        баллов с последующим суммированием баллов по критериям. Итоговое количество
                                        баллов определяется как средняя арифметическая сумма баллов, присвоенных каждым
                                        экспертом по всем критериям оценки (с округлением до тысячных).</p>
                                    <p>Заявка участника конкурса не допускается к рассмотрению на заседании
                                        координационного совета при наличии хотя бы одного заключения о несоответствии
                                        научного проекта.</p>
                                    <p>Департамент в течение пяти дней со дня завершения оценки экспертами последней
                                        заявки формирует общий рейтинг участников конкурса.</p>
                                    <p>Координационный совет не позднее 20 дней со дня получения доступа к просмотру
                                        заявок участников конкурса, экспертным заключениям и общему рейтингу участников
                                        рассматривает заявки и принимает решение о признании участников конкурса
                                        победителями с указанием размера грантов.</p>
                                    <p>Победителями конкурса признаются участники, набравшие наибольшее количество
                                        баллов и имеющие наиболее высокие позиции в общем рейтинге. Победителем конкурса
                                        не может быть признан участник, заявка которого набрала менее 3 баллов.</p>
                                    <p>Координационный совет имеет право принять решение, отличное от мнения экспертов в
                                        части количества баллов, набранных участником конкурса, с последующим изменением
                                        общего рейтинга, обосновав свое решение в соответствии с критериями оценки, и
                                        занесением соответствующей информации в протокол заседания.</p>
                                    <p>Протокол заседания координационного совета, содержащий информацию об общем
                                        рейтинге участников и победителях конкурса, подписывается председателем и
                                        секретарем координационного совета в течение 10 дней с даты проведения заседания
                                        координационного совета и направляется в департамент.</p>
                                </b-card-text>
                            </b-tab>
