                                    <h3>Нормативно-правовая база</h3>
                                    <p>Конкурс проводится в соответствии с Порядком предоставления грантов в форме
                                        субсидий в сфере научной и инновационной деятельности, утвержденным
                                        постановлением от 01.01.2021 № 200.</p>
                                </b-card-text>
